import React from 'react';
import auth from 'solid-auth-client';

/**
 * Base class for React components
 * that need Solid authentication info.
 */
export default class SolidAuthComponent extends React.Component {
  /** Creates a new component with the given properties. */
  constructor(props) {
    super(props);
    this.state = {};
    auth.trackSession(session =>
      this.setState({ webId: session ? session.webId : undefined }));
  }

  /** Whether the user is logged in. */
  get loggedIn() { return !!this.state.webId; }

  /** The WebID of the user, or``undefined` if logged out. */
  get webId() { return this.state.webId; }

  /** Logs the user in. */
  login() { auth.popupLogin(); }

  /** Logs the user out. */
  logout() { auth.logout(); }

  /** Does not render anything. */
  render() { return null; }
}
